'use client';

// we know this page will always be used in a pages router context
// eslint-disable-next-line no-restricted-imports
import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';

import { optionallyRemoveLeadingSlash } from '@/utils/paths/optionallyRemoveLeadingSlash';

interface JWTCallbackProps {
  callbackEndpoint: string;
}

export const JWTCallback = ({ callbackEndpoint }: JWTCallbackProps) => {
  const router = useRouter();
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const handleCallback = async () => {
      const hash = location.hash.slice(1);
      const searchParams = new URLSearchParams(location.search);
      const redirectPath = searchParams.get('redirect');

      try {
        const response = await fetch(callbackEndpoint, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ jwt: hash }),
        });

        if (response.ok) {
          // Auth succeeded, redirect to index of host or specified path
          await router.replace(`/${optionallyRemoveLeadingSlash(redirectPath ?? '')}`);
          return;
        } else {
          const errorText = `Login callback failed (${response.status})`;
          console.error(errorText);
          setError(errorText);
        }
      } catch (error) {
        console.error('Login callback failed:', error);
        setError(`Login callback failed: ${error}`);
      }

      // Only remove hash if we didn't already redirect (error cases)
      await router.replace(location.pathname);
    };

    void handleCallback();
    // we only want the effect to run on page load
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return error ? <div>{error}</div> : <div />;
};
